.ldsRipple {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}
.ldsRipple div {
    position: absolute;
    border: 4px solid rgba(0,114,225, .5);
    opacity: 1;
    border-radius: 50%;
    animation: ldsRipple 3s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.ldsRipple div:nth-child(2) {
    animation-delay: -0.5s;
}
@keyframes ldsRipple {
    0% {
        top: 36px;
        left: 36px;
        width: 0;
        height: 0;
        opacity: 0;
    }
    4.9% {
        top: 36px;
        left: 36px;
        width: 0;
        height: 0;
        opacity: 0;
    }
    5% {
        top: 36px;
        left: 36px;
        width: 0;
        height: 0;
        opacity: 1;
    }
    100% {
        top: 0px;
        left: 0px;
        width: 72px;
        height: 72px;
        opacity: 0;
    }
}

.container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
}
