.header {
    grid-area: h;
    box-shadow: rgba(0,114,225, .5) 0 0 4px 4px;
    margin-bottom: 16px;

}
.loginBlock {
    display: block;
    float: right;
}

.logo {
    height: 100%;
    position: relative;
}
.logo ::after {
    content: '';
    background-color: red;
    display: block;
    position: absolute;
    top: 50px;
    left: 50px;
    z-index: 10;
    width: 100px;
    height: 100px;
    box-shadow: inset rgba(0,114,225, .5) 0 0 4px 4px;
}