
.nav {
    grid-area: n;
    box-shadow: rgba(0,114,225, .5) 0 0 4px 4px;
    padding: 5%;
    margin-right: 10px;
}



ul, li {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
    margin: 5px 0;
}

.item {
    text-decoration: none;
    color: rgba(0,114,225, .9);
}

.active {
    text-decoration: none;
    color: #ffffff;
    background: rgba(0,114,225, .5);
    box-shadow: rgba(0,114,225, .5) 0 0 4px 4px;
}