.item {
    box-shadow: rgba(0,114,225, .5) 0 0 4px 4px;
    padding: 5%;
    margin-top: 12px;
}

.name {
    color: rgb(0,114,225);
    text-transform: uppercase;
    font-weight: bold;
    font-size: 25px;
    margin-left: 15px;
}

.avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.likes {
    background: no-repeat top right 30px/contain url('data:image/png;base64,');
    height: 20px;
    display: block;
    text-align: right;
    color: rgb(0,114,225);
    font-weight: bold;
}

.message {
    border-bottom: 1px solid rgba(0,114,225, .3);
    border-top: 1px solid rgba(0,114,225, .3);
    padding: 15px 0;
}