.app-wrapper {
  display: grid;
  grid-template-areas:
    "h h"
    "n c";
  grid-template-rows: 90px 1fr;
  grid-template-columns: 2fr 10fr;
  width: auto;
  margin: auto;
  padding: 5%;
}

.app-wrapper-content {
  grid-area: c;
  margin-left: 10px;
}



.nav {
  grid-area: n;
  background-color: orange;
}

