.dialogs {
    display: grid;
    grid-template-areas:
    "di m"
    "di nm";
    grid-template-columns: 2fr 10fr;
    grid-template-rows: 10fr 2fr;
}

.dialogsItems {
    background: cyan;
    grid-area: di;
}

.messages {
    background: cyan;
    grid-area: m;
}
.newMessage {
    background-color: red;
    grid-area: nm;
}
